.todoBlock {
  background-color: #f1f1f1;
  color: #333;
  display: flex;
  justify-items: flex-start;
  align-items: center;
  gap: 13px;
  font-size: 32px;
  padding: 15px 30px;
  border-bottom: 2px solid rgb(3, 22, 197);
  border-right: 3px solid rgb(33, 11, 95);
  transition: all 0.1s ease;
}

.todoBlock:hover {
  border-bottom: 2px solid rgb(3, 197, 19);
  border-right: 3px solid rgb(11, 95, 15);
}

.todoBlock:hover {
  background-color: #daffd9;
  cursor: pointer;
}

.todoBlock .todoBlock > svg {
  position: relative;
  top: 4px;
}

.todoBlock:not(:last-child) {
  margin-bottom: 10px;
}

.todoTask {
  color: rgb(233, 61, 152);
}

.todoText {
  font-style: italic;
  color: rgb(27, 42, 170);
}
.todoBlock:hover .todoText {
  color: #1e5309;
}
.todoBlock:hover .todoTask {
  color: rgb(128, 4, 4);
}
.todoText::first-letter {
  text-transform: uppercase;
}
