.todoFormContainer {
  margin-bottom: 78px;
}

.todoFormContainer input {
  all: unset;
  box-sizing: border-box;
  text-align: left;
  background-color: rgb(240, 240, 240);
  border-radius: 10px;
  font-size: 22px;
  height: 50px;
  width: 350px;
  color: rgb(67, 81, 141);
  padding: 2px 10px;
  cursor: text;
  font-weight: 600;
  border: 2px solid rgb(131, 148, 224);
  margin-right: 25px;
  transition: all 0.1s ease;
}

.todoFormContainer input:hover {
  background-color: #e9f0ff;
  box-shadow: 0 0 7px rgb(156, 167, 216);
}

.todoFormContainer input:focus {
  background-color: #f6f8fd;

  box-shadow: 0 0 5px rgb(146, 158, 211);
}

.todoFormContainer button {
  text-transform: uppercase;
  border: none;
  background-color: #5b6cca;
  color: rgb(189, 198, 240);
  border-radius: 7px;
  padding: 12px 26px;
  font-size: 24px;
  font-weight: 400;
  transition: 0.27s all ease-out;
  letter-spacing: 1.23px;
  cursor: pointer;
}

.todoFormContainer button:hover {
  background-color: #697ada;
  color: rgb(204, 212, 248);
}

.todoFormContainer button:active {
  transition: 0s all ease;
  /* background-color: #9de5b3;
  color: rgb(35, 105, 33); */
  color: #5b6cca;
  background-color: rgb(189, 198, 240);
}
