.App {
  text-align: center;
  padding-top: 70px;
  max-width: 50%;
  margin: 0 auto;
}

h1 {
  color: #ffffff;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 50px;
  transition: 0.2s all ease;
}

h1 > span {
  transition: 0.2s all ease;
}

h1 > span:hover {
  /* box-shadow: 8px 4px 15px #4840bd; */
  color: #efeefc;
}
